import { contentType, size } from "./apple-icon.tsx"
import { fillMetadataSegment } from 'next/dist/lib/metadata/get-metadata-route'

const imageModule = { contentType, size }

export default async function (props) {
    const { __metadata_id__: _, ...params } = await props.params
    const imageUrl = fillMetadataSegment("/", params, "apple-icon")

    function getImageMetadata(imageMetadata, idParam) {
        const data = {
            alt: imageMetadata.alt,
            type: imageMetadata.contentType || 'image/png',
            url: imageUrl + (idParam ? ('/' + idParam) : '') + "?a68fe26bcf0ea303",
        }
        const { size } = imageMetadata
        if (size) {
            data.sizes = `${size.width}x${size.height}`;
        }
        return data
    }

    return [getImageMetadata(imageModule, '')]
}
